import { Component, onMount, onCleanup, createSignal, For } from 'solid-js';
import * as Cesium from 'cesium';
import { CONFIG } from '@/config';
import type { HistoricalSite, Position } from '@/types';

interface CesiumMapProps {
  onPlayerMove: (latitude: number, longitude: number) => void;
  onHistoricalSiteClick: (site: HistoricalSite) => void;
}

interface MapLocation {
  id: string;
  name: string;
  type: 'landmark' | 'poi' | 'user';
  lat: number;
  lng: number;
  description?: string;
}

const CesiumMap: Component<CesiumMapProps> = (props) => {
  let mapContainer: HTMLDivElement;
  let viewer: Cesium.Viewer | undefined;

  const [playerPosition, setPlayerPosition] = createSignal<Position>({
    latitude: 25.0330,
    longitude: 121.5654,
  });

  const [mapMode, setMapMode] = createSignal<'satellite' | 'terrain' | 'street'>('satellite');
  const [isLoading, setIsLoading] = createSignal(true);

  // Mock locations for demonstration
  const sampleLocations: MapLocation[] = [
    {
      id: '1',
      name: '101',
      type: 'landmark',
      lat: 25.0338,
      lng: 121.5645,
      description: ''
    },
    {
      id: '2',
      name: '',
      type: 'poi',
      lat: 25.0408,
      lng: 121.5598,
      description: ''
    },
    {
      id: '3',
      name: '',
      type: 'user',
      lat: playerPosition().latitude,
      lng: playerPosition().longitude,
      description: ''
    }
  ];

  onMount(async () => {
    console.log(' Initializing CesiumJS 3D Map');

    try {
      // Set Cesium access token
      Cesium.Ion.defaultAccessToken = CONFIG.cesium.accessToken;

      // Initialize Cesium viewer
      viewer = new Cesium.Viewer(mapContainer, {
        terrain: Cesium.Terrain.fromWorldTerrain(),
        baseLayerPicker: false,
        geocoder: false,
        homeButton: false,
        sceneModePicker: false,
        navigationHelpButton: false,
        animation: false,
        timeline: false,
        fullscreenButton: false,
        vrButton: false,
        scene3DOnly: true,
      });

      //  viewer 
      (window as any).cesiumViewer = viewer;

      // 
      viewer.scene.globe.show = true;
      viewer.scene.skyBox.show = true;

      // 
      viewer.scene.backgroundColor = Cesium.Color.DARKSLATEGRAY;

      // 
      viewer.scene.globe.enableLighting = false;
      viewer.scene.fog.enabled = false;
      viewer.scene.skyAtmosphere.show = true;

      // Set initial camera position
      viewer.camera.setView({
        destination: Cesium.Cartesian3.fromDegrees(
          CONFIG.cesium.defaultView.longitude,
          CONFIG.cesium.defaultView.latitude,
          CONFIG.cesium.defaultView.height
        ),
      });

      // Add click event listener
      viewer.cesiumWidget.screenSpaceEventHandler.setInputAction((event: any) => {
        const pickedPosition = viewer!.camera.pickEllipsoid(event.position, viewer!.scene.globe.ellipsoid);
        if (pickedPosition) {
          const cartographic = Cesium.Cartographic.fromCartesian(pickedPosition);
          const longitude = Cesium.Math.toDegrees(cartographic.longitude);
          const latitude = Cesium.Math.toDegrees(cartographic.latitude);

          setPlayerPosition({ latitude, longitude });
          props.onPlayerMove(latitude, longitude);
        }
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK);

      setIsLoading(false);
      console.log(' CesiumJS 3D Map initialized successfully');
    } catch (error) {
      console.error('Failed to initialize CesiumJS:', error);
      setIsLoading(false);
    }
  });

  onCleanup(() => {
    if (viewer) {
      viewer.destroy();
    }
  });

  const handleMapClick = (event: MouseEvent) => {
    const rect = mapContainer.getBoundingClientRect();
    const x = ((event.clientX - rect.left) / rect.width - 0.5) * 360;
    const y = ((rect.bottom - event.clientY) / rect.height - 0.5) * 180;

    console.log(`Map clicked at: ${y.toFixed(6)}, ${x.toFixed(6)}`);
    setPlayerPosition({ latitude: y, longitude: x });
    props.onPlayerMove(y, x);
  };

  const getLocationIcon = (type: string) => {
    switch (type) {
      case 'landmark':
        return 'M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5';
      case 'poi':
        return 'M15 10.5a3 3 0 11-6 0 3 3 0 016 0z M19.5 10.5c0 7.142-7.5 11.25-7.5 11.25S4.5 17.642 4.5 10.5a7.5 7.5 0 1115 0z';
      case 'user':
        return 'M15 10.5a3 3 0 11-6 0 3 3 0 016 0z M19.5 10.5c0 7.142-7.5 11.25-7.5 11.25S4.5 17.642 4.5 10.5a7.5 7.5 0 1115 0z';
      default:
        return 'M15 10.5a3 3 0 11-6 0 3 3 0 016 0z';
    }
  };

  return (
    <div class="w-full h-full relative overflow-hidden">
      {/* CesiumJS Map Container */}
      <div
        ref={mapContainer!}
        class="w-full h-full"
        style="min-height: 400px; height: calc(100vh - 65px);"
      >

      </div>

      {/* Loading State */}
      {isLoading() && (
        <div class="absolute inset-0 bg-white/95 backdrop-blur-sm flex items-center justify-center z-30">
          <div class="text-center space-y-4 animate-scale-in">
            <div class="w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-2xl flex items-center justify-center mx-auto shadow-lg animate-pulse-subtle">
              <svg class="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V9a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2z" />
              </svg>
            </div>
            <div class="space-y-2">
              <h3 class="text-heading-sm text-primary"> CesiumJS 3D </h3>
              <p class="text-body-md text-secondary">...</p>
            </div>
          </div>
        </div>
      )}

      {/* Position Display */}
      <div class="absolute bottom-6 left-6 z-20">
        <div class="card p-4 space-y-2">
          <div class="flex items-center space-x-2">
            <div class="w-2 h-2 bg-blue-500 rounded-full animate-pulse-subtle"></div>
            <span class="text-body-sm text-secondary"></span>
          </div>
          <div class="space-y-1">
            <p class="text-body-sm text-primary font-mono">
              : {playerPosition().latitude.toFixed(6)}
            </p>
            <p class="text-body-sm text-primary font-mono">
              : {playerPosition().longitude.toFixed(6)}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CesiumMap;